:root {
	--primaryColor: #00eaff;
	--primaryHover: #00b9c9;
	--secondaryColor: rgb(150, 162, 188);
	--paragraphColor: rgb(150, 162, 188);
	--BgColor: #031a30;
	--formColor: rgb(2, 15, 29);
	--projectCardColor: #072340;
	--textColor: #dae4fb;
	--headerHeight: 3rem;
}

.footer {
	width: 100%;
	text-align: center;
	background: var(--projectCardColor);
	margin-top: 1rem;
	padding: 1rem 0;
}
.footer span {
	text-align: center;
	color: var(--secondaryColor);
	opacity: 0.5;
	font-size: 13px;
}
.footer span a {
	color: var(--secondaryColor);
}
.footer span a:hover {
	color: var(--primaryColor);
}

